// /src/App.js
import React, { Suspense, lazy, useEffect } from 'react';
import { Routes, Route, Navigate } from 'react-router-dom';
import { ThemeProvider, createTheme } from '@mui/material/styles';
import { SnackbarProvider } from 'notistack';
import CssBaseline from '@mui/material/CssBaseline';
import GlobalStyle from './GlobalStyle';
import PrivateRoute from './components/PrivateRoute';
import ErrorBoundary from './components/ErrorBoundary';
import axios from 'axios';
import CircularProgress from '@mui/material/CircularProgress';
import PropTypes from 'prop-types';

// Define API Base URL from environment variables or fallback to localhost
const API_URL = process.env.REACT_APP_API_BASE_URL || 'https://localhost:3001/api';

// Lazy load pages to improve performance by code splitting
const Dashboard = lazy(() => import('./features/dashboard/pages/Dashboard'));
const Documents = lazy(() => import('./features/documents/pages/Documents'));
const Profile = lazy(() => import('./features/profile/pages/Profile'));
const AdminPanel = lazy(() => import('./features/admin/pages/AdminPanel'));
const Chat = lazy(() => import('./features/chat/pages/Chat'));
const Instructions = lazy(() => import('./features/instructions/pages/Instructions'));
const DocumentDetails = lazy(() => import('./features/documents/pages/DocumentDetails'));
const DocumentForm = lazy(() => import('./features/documents/pages/DocumentForm'));
const UserManagement = lazy(() => import('./features/admin/pages/UserManagement'));
const ErrorPage = lazy(() => import('./features/shared/pages/ErrorPage'));
const Login = lazy(() => import('./features/auth/pages/Login'));

// Create a Material UI theme with professional styling
const muiTheme = createTheme({
  palette: {
    primary: {
      main: '#0d47a1', // Professional dark blue
    },
    secondary: {
      main: '#9e9e9e', // Gray for secondary elements
    },
    background: {
      default: '#f5f5f5', // Light gray background
      paper: '#ffffff', // White paper elements
    },
    text: {
      primary: '#212121', // Dark text for primary content
      secondary: '#757575', // Medium gray for secondary text
    },
    error: {
      main: '#d32f2f', // Red for errors
    },
  },
  typography: {
    fontFamily: '"Roboto", "Helvetica", "Arial", sans-serif',
    h1: { fontSize: '2.5rem', fontWeight: 500, marginBottom: '1rem' },
    h2: { fontSize: '2rem', fontWeight: 500, marginBottom: '0.8rem' },
    h3: { fontSize: '1.75rem', fontWeight: 500, marginBottom: '0.5rem' },
  },
  components: {
    MuiButton: {
      styleOverrides: { root: { textTransform: 'none' } },
    },
    MuiPaper: {
      styleOverrides: { root: { padding: '20px', boxShadow: '0px 2px 4px rgba(0,0,0,0.1)' } },
    },
  },
});

// A robust Loading component using CircularProgress with additional text.
const Loading = () => (
  <div
    style={{
      display: 'flex',
      flexDirection: 'column',
      justifyContent: 'center',
      alignItems: 'center',
      height: '100vh',
    }}
  >
    <CircularProgress color="primary" />
    <p style={{ marginTop: '16px', fontSize: '1.2rem', color: muiTheme.palette.text.primary }}>
      Loading...
    </p>
  </div>
);

const App = () => {
  useEffect(() => {
    // Create an axios cancellation token to cancel the request if needed
    const source = axios.CancelToken.source();

    // Test the server connection on component mount
    const testServerConnection = async () => {
      try {
        const response = await axios.get(`${API_URL}/test`, {
          withCredentials: true,
          cancelToken: source.token,
        });
        console.log('Test response:', response.data);
      } catch (error) {
        if (axios.isCancel(error)) {
          console.log('Server connection test cancelled:', error.message);
        } else {
          console.error('Error testing server connection:', error.message);
        }
      }
    };

    testServerConnection();
    return () => source.cancel('Component unmounted, cancelling server test.');
  }, []);

  return (
    <ThemeProvider theme={muiTheme}>
      <CssBaseline />
      <GlobalStyle />
      <SnackbarProvider maxSnack={3} anchorOrigin={{ vertical: 'top', horizontal: 'right' }}>
        <ErrorBoundary>
          <Suspense fallback={<Loading />}>
            <Routes>
              <Route path="/" element={<Navigate to="/dashboard" replace />} />
              <Route path="/login" element={<Login />} />
              <Route path="/dashboard" element={<PrivateRoute><Dashboard /></PrivateRoute>} />
              <Route path="/documents" element={<PrivateRoute><Documents /></PrivateRoute>} />
              <Route path="/documents/new" element={<PrivateRoute><DocumentForm /></PrivateRoute>} />
              <Route path="/documents/:id" element={<PrivateRoute><DocumentDetails /></PrivateRoute>} />
              <Route path="/profile" element={<PrivateRoute><Profile /></PrivateRoute>} />
              <Route path="/admin" element={<PrivateRoute><AdminPanel /></PrivateRoute>} />
              <Route path="/admin/users" element={<PrivateRoute><UserManagement /></PrivateRoute>} />
              <Route path="/chat" element={<PrivateRoute><Chat /></PrivateRoute>} />
              <Route path="/instructions" element={<PrivateRoute><Instructions /></PrivateRoute>} />
              <Route path="/error" element={<ErrorPage />} />
              {/* Redirect unknown routes back to dashboard */}
              <Route path="*" element={<Navigate to="/dashboard" replace />} />
            </Routes>
          </Suspense>
        </ErrorBoundary>
      </SnackbarProvider>
    </ThemeProvider>
  );
};

// Validate that PrivateRoute receives valid React nodes
PrivateRoute.propTypes = {
  children: PropTypes.node.isRequired,
};

export default App;
