// /src/index.js
import React from 'react';
import { createRoot } from 'react-dom/client';
import { Provider } from 'react-redux';
import { PersistGate } from 'redux-persist/integration/react';
import { BrowserRouter } from 'react-router-dom';
import { SnackbarProvider } from 'notistack';
import App from './App';
import { store, persistor } from './store';
import reportWebVitals from './reportWebVitals';
import CircularProgress from '@mui/material/CircularProgress';

/**
 * Loading component used by PersistGate until the persisted state is rehydrated.
 * It uses Material UI's CircularProgress for a professional spinner.
 */
const Loading = () => (
    <div
        style={{
            display: 'flex',
            justifyContent: 'center',
            alignItems: 'center',
            height: '100vh',
        }}
        aria-label="Loading application"
        role="alert"
    >
        <CircularProgress color="primary" />
    </div>
);

/**
 * RootErrorBoundary catches any error in the component tree and displays
 * a fallback UI. This is critical for production apps to prevent blank screens.
 */
class RootErrorBoundary extends React.Component {
    constructor(props) {
        super(props);
        this.state = { hasError: false };
    }

    // Update state when an error is encountered
    static getDerivedStateFromError() {
        return { hasError: true };
    }

    // Log error details (integrate with error-monitoring services like Sentry)
    componentDidCatch(error, errorInfo) {
        console.error('RootErrorBoundary caught an error:', error, errorInfo);
    }

    render() {
        if (this.state.hasError) {
            return (
                <div
                    style={{
                        display: 'flex',
                        flexDirection: 'column',
                        justifyContent: 'center',
                        alignItems: 'center',
                        height: '100vh',
                        textAlign: 'center',
                        padding: '1rem',
                    }}
                >
                    <h1>Something went wrong</h1>
                    <p>Please refresh the page or try again later.</p>
                </div>
            );
        }
        return this.props.children;
    }
}

// Grab the root container from the HTML file
const container = document.getElementById('root');
const root = createRoot(container);

// Render the application tree with all providers and error boundaries
root.render(
    <Provider store={store}>
        <PersistGate loading={<Loading />} persistor={persistor}>
            <BrowserRouter>
                <SnackbarProvider
                    maxSnack={3}
                    anchorOrigin={{ vertical: 'top', horizontal: 'right' }}
                    autoHideDuration={4000}
                >
                    <RootErrorBoundary>
                        <App />
                    </RootErrorBoundary>
                </SnackbarProvider>
            </BrowserRouter>
        </PersistGate>
    </Provider>
);

// Optionally report web vitals for performance monitoring
reportWebVitals(console.log);
