import React from 'react';
import styled from 'styled-components';

// Styled component for the error message container
const ErrorContainer = styled.div`
  padding: 20px;
  text-align: center;
  color: #dc3545;
  border: 1px solid #f5c6cb;
  border-radius: 5px;
  background-color: #f8d7da;
  margin: 20px auto;
  max-width: 800px;
`;

// Styled component for the "Try again" button
const TryAgainButton = styled.button`
  background-color: #007bff; // Primary button color
  color: white;
  border: none;
  padding: 10px 20px;
  margin-top: 15px; // Add some space above the button
  cursor: pointer;
  border-radius: 5px;
  font-size: 1rem;

  &:hover {
    background-color: #0062cc; // Darker shade on hover
  }
`;

class ErrorBoundary extends React.Component {
    constructor(props) {
        super(props);
        this.state = { hasError: false, error: null, errorInfo: null, remountKey: 0 }; // Add remountKey
    }

    static getDerivedStateFromError(error) {
        return { hasError: true };
    }

    componentDidCatch(error, errorInfo) {
        console.error('ErrorBoundary caught an error:', error, errorInfo);
        this.setState({ error, errorInfo });
    }

    // Function to regenerate key, causing remount of the component tree
    regenerateKey = () => {
        this.setState((prevState) => ({
            hasError: false,
            error: null,
            errorInfo: null,
            remountKey: prevState.remountKey + 1
        }));
    };

    render() {
        if (this.state.hasError) {
            return (
                <ErrorContainer>
                    <h2>Something went wrong.</h2>
                    {/* Optionally display more detailed error information for debugging */}
                    {process.env.NODE_ENV === 'development' && (
                        <details>
                            <summary>Error Details</summary>
                            <p>{this.state.error?.message}</p>
                            <pre>{this.state.errorInfo?.componentStack}</pre>
                        </details>
                    )}
                    {/* "Try again" button with logic to reset the error state and remount the tree */}
                    <TryAgainButton onClick={this.regenerateKey}>
                        Try again
                    </TryAgainButton>
                </ErrorContainer>
            );
        }

        // Use the key prop to force remount when remountKey changes
        return <React.Fragment key={this.state.remountKey}>{this.props.children}</React.Fragment>;
    }
}

export default ErrorBoundary;